body {
  --header-height: 60px;
  --active-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  width: 40px;
  height: 40px;
  display: block;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  height: var(--header-height);
  line-height: var(--header-height);
  text-transform: uppercase;
  color: #000;
  display: block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
